<h1 id="顺序表" tabindex="-1"><a class="header-anchor" href="#顺序表" aria-hidden="true">#</a> 顺序表</h1>
<nav class="table-of-contents"><ul><li><RouterLink to="#什么是顺序表">什么是顺序表？</RouterLink></li><li><RouterLink to="#顺序表的初始化">顺序表的初始化</RouterLink></li><li><RouterLink to="#顺序表插入元素">顺序表插入元素</RouterLink></li><li><RouterLink to="#顺序表删除元素">顺序表删除元素</RouterLink></li><li><RouterLink to="#顺序表查找元素">顺序表查找元素</RouterLink></li><li><RouterLink to="#顺序表更改元素">顺序表更改元素</RouterLink></li></ul></nav>
<h2 id="什么是顺序表" tabindex="-1"><a class="header-anchor" href="#什么是顺序表" aria-hidden="true">#</a> 什么是顺序表？</h2>
<blockquote>
<p>顺序表，全名顺序存储结构，是线性表的一种。通过《什么是线性表》一节的学习我们知道，线性表用于存储逻辑关系为“一对一”的数据，顺序表自然也不例外。</p>
</blockquote>
<p>不仅如此，顺序表对数据的物理存储结构也有要求。顺序表存储数据时，会提前申请一整块足够大小的物理空间，然后将数据依次存储起来，存储时做到数据元素之间不留一丝缝隙。</p>
<p>例如，使用顺序表存储集合 {1,2,3,4,5}，数据最终的存储状态如图 1 所示：</p>
<img :src="$withBase('/images/dataStructure/linear_list_seq_1.png')" alt="linear_list_seq_1">
<p>图 1 顺序存储结构示意图</p>
<p>由此我们可以得出，将“具有 '一对一' 逻辑关系的数据按照次序连续存储到一整块物理空间上”的存储结构就是顺序存储结构。</p>
<p>通过观察图 1 中数据的存储状态，我们可以发现，顺序表存储数据同数组非常接近。其实，顺序表存储数据使用的就是数组。</p>
<h2 id="顺序表的初始化" tabindex="-1"><a class="header-anchor" href="#顺序表的初始化" aria-hidden="true">#</a> 顺序表的初始化</h2>
<blockquote>
<p>使用顺序表存储数据之前，除了要申请足够大小的物理空间之外，为了方便后期使用表中的数据，顺序表还需要实时记录以下 2 项数据：</p>
<ol>
<li>顺序表申请的存储容量；</li>
<li>顺序表的长度，也就是表中存储数据元素的个数；</li>
</ol>
</blockquote>
<ul>
<li>提示：正常状态下，顺序表申请的存储容量要大于顺序表的长度。</li>
</ul>
<h2 id="顺序表插入元素" tabindex="-1"><a class="header-anchor" href="#顺序表插入元素" aria-hidden="true">#</a> 顺序表插入元素</h2>
<blockquote>
<p>向已有顺序表中插入数据元素，根据插入位置的不同，可分为以下 3 种情况：</p>
<ol>
<li>插入到顺序表的表头；</li>
<li>在表的中间位置插入元素；</li>
<li>尾随顺序表中已有元素，作为顺序表中的最后一个元素；</li>
</ol>
</blockquote>
<blockquote>
<p>虽然数据元素插入顺序表中的位置有所不同，但是都使用的是同一种方式去解决，即：通过遍历，找到数据元素要插入的位置，然后做如下两步工作：</p>
<ol>
<li>将要插入位置元素以及后续的元素整体向后移动一个位置；</li>
<li>将元素放到腾出来的位置上；</li>
</ol>
</blockquote>
<img :src="$withBase('/images/dataStructure/linear_list_seq_2.png')" alt="linear_list_seq_2.png">
<div class="custom-container tip"><p class="custom-container-title">注意，动态数组额外申请更多物理空间使用的是 realloc 函数。</p>
<p>并且，在实现后续元素整体后移的过程，目标位置其实是有数据的，还是 3，只是下一步新插入元素时会把旧元素直接覆盖。</p>
</div>
<h2 id="顺序表删除元素" tabindex="-1"><a class="header-anchor" href="#顺序表删除元素" aria-hidden="true">#</a> 顺序表删除元素</h2>
<div class="custom-container tip"><p class="custom-container-title">从顺序表中删除指定元素，实现起来非常简单，只需找到目标元素，并将其后续所有元素整体前移 1 个位置即可。</p>
<p>后续元素整体前移一个位置，会直接将目标元素删除，可间接实现删除元素的目的。</p>
</div>
<h2 id="顺序表查找元素" tabindex="-1"><a class="header-anchor" href="#顺序表查找元素" aria-hidden="true">#</a> 顺序表查找元素</h2>
<div class="custom-container tip"><p class="custom-container-title">顺序表中查找目标元素，可以使用多种查找算法实现，比如说二分查找算法、插值查找算法等。</p>
</div>
<h2 id="顺序表更改元素" tabindex="-1"><a class="header-anchor" href="#顺序表更改元素" aria-hidden="true">#</a> 顺序表更改元素</h2>
<div class="custom-container tip"><p class="custom-container-title">顺序表更改元素的实现过程是：</p>
<ol>
<li>找到目标元素；</li>
<li>直接修改该元素的值；</li>
</ol>
</div>
<p>C 语言操作顺序表的实现代码为：</p>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>#include &lt;stdio.h>
#include &lt;stdlib.h>
#define Size 5
typedef struct Table{
    int * head;
    int length;
    int size;
}table;
table initTable(){
    table t;
    t.head=(int*)malloc(Size*sizeof(int));
    if (!t.head)
    {
        printf("初始化失败\n");
        exit(0);
    }
    t.length=0;
    t.size=Size;
    return t;
}
table addTable(table t,int elem,int add)
{
    if (add>t.length+1||add&lt;1) {
        printf("插入位置有问题\n");
        return t;
    }
    if (t.length>=t.size) {
        t.head=(int *)realloc(t.head, (t.size+1)*sizeof(int));
        if (!t.head) {
            printf("存储分配失败\n");
        }
        t.size+=1;
    }
    for (int i=t.length-1; i>=add-1; i--) {
        t.head[i+1]=t.head[i];
    }
    t.head[add-1]=elem;
    t.length++;
    return t;
}
table delTable(table t,int add){
    if (add>t.length || add&lt;1) {
        printf("被删除元素的位置有误\n");
        return t;
    }
    for (int i=add; i&lt;t.length; i++) {
        t.head[i-1]=t.head[i];
    }
    t.length--;
    return t;
}
int selectTable(table t,int elem){
    for (int i=0; i&lt;t.length; i++) {
        if (t.head[i]==elem) {
            return i+1;
        }
    }
    return -1;
}
table amendTable(table t,int elem,int newElem){
    int add=selectTable(t, elem);
    t.head[add-1]=newElem;
    return t;
}
void displayTable(table t){
    for (int i=0;i&lt;t.length;i++) {
        printf("%d ",t.head[i]);
    }
    printf("\n");
}
int main(){
    table t1=initTable();
    for (int i=1; i&lt;=Size; i++) {
        t1.head[i-1]=i;
        t1.length++;
    }
    printf("原顺序表：\n");
    displayTable(t1);
  
    printf("删除元素1:\n");
    t1=delTable(t1, 1);
    displayTable(t1);
  
    printf("在第2的位置插入元素5:\n");
    t1=addTable(t1, 5, 2);
    displayTable(t1);
  
    printf("查找元素3的位置:\n");
    int add=selectTable(t1, 3);
    printf("%d\n",add);
  
    printf("将元素3改为6:\n");
    t1=amendTable(t1, 3, 6);
    displayTable(t1);
    return 0;
}
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br><span class="line-number">30</span><br><span class="line-number">31</span><br><span class="line-number">32</span><br><span class="line-number">33</span><br><span class="line-number">34</span><br><span class="line-number">35</span><br><span class="line-number">36</span><br><span class="line-number">37</span><br><span class="line-number">38</span><br><span class="line-number">39</span><br><span class="line-number">40</span><br><span class="line-number">41</span><br><span class="line-number">42</span><br><span class="line-number">43</span><br><span class="line-number">44</span><br><span class="line-number">45</span><br><span class="line-number">46</span><br><span class="line-number">47</span><br><span class="line-number">48</span><br><span class="line-number">49</span><br><span class="line-number">50</span><br><span class="line-nu